.a-logo, .n-logo{
    color: white;
    font-size: 6rem;
    text-shadow: 5px 5px 1px  black ;

}
.n-logo{
    position: relative;
    left: -4vh;
    color: black;
    text-shadow: 5px 5px 1px white ;

}
.n-logo{
    transform: rotate(44deg);
    text-shadow: 5px 5px 1px rgba(0, 0, 0, 0.404) ;

}
nav{
width: 50%;
            position: relative;
            left: 33vw;
        }
        .head{
            height: 12vh;
            width: 100%;
            background-color: lightgray;
            position: relative;
            border: 1px solid black;
            &:before{
                position: absolute;
                content: "";
                top: 33%;
                right: 50%;
                left: 20%;
                bottom: 0;
                border-radius: 50%;
                height: 12vh;
                width: 66vw;
                background-color: lightgray;   
            }
        }
        
        .nav-li{     
            display: flex;
            list-style: none;
        height: 1vh;
        position: relative;
        top: -8vh;
        }
        .a{
            list-style: none;
            font-family: Rowdies;
            text-decoration: none;
            margin: 1rem;
            font-size: 1.2rem;
        }
@media (max-width: 1024px){
    .a-logo, .n-logo{
    
        font-size: 3rem
    }
    nav{
        width: 75%;
        left: 22vw;
    }
    .nav-li{
    
    }
.a{
    margin: 10px;
}
}


@media (max-width: 750px){
    nav{
        width: 80%;
        left: 12vw;
    }
    .a{
        font-size: 1rem;
        color: red;
        
    }
    
}
@media (max-width: 420px){
    
    

    .a-logo, .n-logo{
        font-size: 2rem;
        position: relative;
        top: -3vh;
    
    }
    
        nav{
            position: fixed;
            width: 0;
            &::after{
content: "<<";
width: 0;
position: absolute;
      height: 0;
      color: black;
      top: 5vh;
      left: 16vw;
      right: 0vw;
      position: absolute;
      font-size: 2rem;
      cursor: pointer;
      animation: nav 900ms infinite ease-in-out;


      @keyframes nav {
          from{
            transform: scale(1);
          }
          to{
            transform: scale(0.5);


          }
          
      }
            }
          }
          .nav-ul{
            padding: 0;
            margin: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 10%;
            width: 33%;
            margin-bottom: -22vh;
          }
          .nav-li{
            display: flex;
         border: 1rem solid white; 
            flex-direction: column;
            justify-content: center;
            align-items: center;
            font-size: 1rem;
            color: white;
            text-align: center;
            width: 14vw;
            height: 8vh;
            max-width: auto;

          }
          .a{
            
            display: flex;
            align-items: center;
            text-align: center;
            height: 3rem;
            transition: 400ms ease;
            margin: auto;
            opacity: (0);
            

          }
          nav:hover .a{
            filter: grayscale(0%);
            opacity: (1);
          }
          
}